$background: #FFFBF2;
$primary-color: #EC0100;
$secondary-color: #1E3456;
$container: 120.75rem;
$primary-font: 'PP Monument Extended';


html {
    margin: 0;
    padding: 0;
    font-size: calc(100vw / 1920 * 10);
}

@media (max-width: 991px) {
    html {
        font-size: calc(100vw / 1920 * 15);
    }

    body {
        padding: 0 4rem;
    }
}

@media (max-width: 550px) {
    html {
        font-size: calc(100vw / 1920 * 20);
    }
}

@media (max-width: 450px) {
    html {
        font-size: calc(100vw / 1920 * 25);
    }
}

body {
    background: $background;
    width: 100%;
    height: 100%;
    box-sizing: border-box;
    margin: 0;
    overflow-x: hidden;
    opacity: 0;
}

.preloader {
    position: fixed;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    background-color: $background;
    z-index: 99;
    clip-path: inset(0 0% 0 0%);
    transition: 0.5s;
}

.open {
    clip-path: inset(0 50% 0 50%);
}

.preloader .content-holder {
    width: 22rem;
    position: absolute;
    right: 10%;
    bottom: 5%;
    display: block;
    text-align: center;
}

.preloader #counter {
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    font-family: $primary-font;
    color: $primary-color;
    font-size: 5.5rem;
    font-weight: 500;
    margin-bottom: 1.5rem;
}

.preloader #loader {
    width: 0;
    height: 0.5rem;
    background-color: $primary-color;
    display: block;
}

.container {
    max-width: $container;
    margin: 0 auto;
    width: 100%;
}

h1, h2, h3 {
    margin: 0;
}

.section1 {
 padding-top: 25rem;
 padding-bottom: 30rem;
}

.section1 h1 {
    color: $primary-color;
    font-family: $primary-font;
    font-size: 8.3rem;
    line-height: 1;
    text-align: center;
}

.section1 .image-holder img {
    height: 120.17rem;
    object-fit: cover;
    width: 100%;
}

.section1 .row1 {
    width: 100%;
    display: flex;
    justify-content: space-between;
}

.section1 h2,
.section1 h3 {
    font-size: 1.8rem;
    color: $primary-color;
    font-family: $primary-font;
    margin-top: 4rem;
}

.greece {
    color: $secondary-color;
}

.section2 .row1 {
    display: flex;
    justify-content: space-between;
    align-items: end;
    margin-bottom: 5rem;
}

.section2 .row1 h2 {
    font-size: 3.2rem;
    color: $primary-color;
    font-family: $primary-font;
}

.section2 .row1 h2 .line2 {
    text-indent: 5rem;
}

.section2 .row1 h2 .lines {
    margin-bottom: 1rem;
}

.section2 .row1 h2 .lines:nth-last-child(1) {
    margin-bottom: 0;
}

.section2 .row1 h2 .line3 {
    text-indent: 10rem;
}

.section2 .full-container {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: 100%;
    overflow: hidden;
}

.section2 .since-col {
    width: 36rem;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    position: relative;
    margin-top: -6rem;
}

.section2 .carousel-col {
    width: 156rem;
}

.section2 .slider-nav img {
    width: 6.56rem;
}

.section2 .swiper-wrapper {
    transition: transform 1s cubic-bezier(0.83, 0, 0.17, 1) !important;
}

.section2 .swiper-slide {
    height: 70.8rem;
    background-size: cover;
    background-repeat: no-repeat;
    width: 100% !important;
    background-position: center;
}

.section2 .swiper-slide:nth-child(1) {
    background-image: url('/scss/images/portfolio-images/shoreditch.jpg');
}

.section2 .swiper-slide:nth-child(2) {
    background-image: url('/scss/images/portfolio-images/panopoulos.jpg');
}

.section2 .swiper-slide:nth-child(3) {
    background-image: url('/scss/images/portfolio-images/mezen.jpg');
}

.section2 .swiper-slide:nth-child(4) {
    background-image: url('/scss/images/portfolio-images/ilvief.jpg');
}

.section2 .swiper-slide:nth-child(5) {
    background-image: url('/scss/images/portfolio-images/thenutrihub.jpg');
}

.section2 {
    margin-bottom: 10rem;
}

.section2 .swiper {
    padding-bottom: 6rem;
    padding-right: 33rem;
}

.section2 .swiper h3 {
    position: absolute;
    top: calc(100% + 4rem);
    left: 0;
}

.section2 .swiper .swiper-slide a {
    display: flex;
    align-items: center;
    justify-content: center;
    position: absolute;
    right: 10rem;
    bottom: 10rem;
    width: 18.1rem;
    height: 18.1rem;
    background-color: #fff;
    border-radius: 50%;
}

.section2 .swiper .swiper-slide a:hover img:first-child {
    margin-top: -8rem;
    margin-left: 8rem;
}

.section2 .swiper .swiper-slide a img:nth-child(2) {
    margin-left: -8rem;
    margin-top: -1rem;
}

.section2 .swiper .swiper-slide a:hover img:nth-child(2) {
    margin-left: -0.5rem;
}

.section2 .swiper .swiper-slide a img {
    width: 8rem;
    transition: margin 0.5s;
}

.section2 .swiper .swiper-slide a .arrow-holder {
    overflow: hidden;
    display: block;
    width: 8rem;
    height: 8rem;
}

.section2 .since-col h3 {
    font-size: 3rem;
    font-weight: 100;
    letter-spacing: 2rem;
    font-family: "PP Monument Extended";
    color: #EC0100;
    max-width: 17rem;
    word-break: break-word;
    text-align: center;
    line-height: 5rem;
    position: relative;
}

.section2 .since-col h3:before {
    content: "";
    position: absolute;
    left: 50%;
    transform: translateX(calc(-50% - 1rem));
    width: 0.2rem;
    height: 5rem;
    display: block;
    top: 6rem;
    opacity: 18%;
    background-color: $secondary-color;
}

.section2 .since-col span {
    position: absolute;
    left: 48%;
    transform: translateX(-50%);
    bottom: -8rem;
    font-size: 1.8rem;
    color: $secondary-color;
    font-weight: 800;
    font-family: $primary-font;
}

.section2 .swiper-slide h3 {
    color: $primary-color;
    font-family: $primary-font;
    font-size: 1.8rem;
}

@media (max-width: 991px) {
    .section2 .since-col {
        width: 20%;
    }

    .section2 .carousel-col {
        width: 80%;
    }

    .section2 .swiper-slide {
        width: 95rem !important;
        height: 61.8rem;
    }

    .section2 .swiper {
        padding-right: 0;
    }

    .section2 .swiper-slide {
        width: 100% !important;
    }
}

@media (max-width: 550px) {

}

.section3 {
    font-size: 10rem;
    height: 100vh;
    margin-top: 30rem;
    display: flex;
    align-items: center;
    width: 100%;
}

.section3 .row1,
.section3 .row2 {
    display: flex;
    justify-content: space-between;
}

.section3 .row1 {
    display: flex;
    align-items: end;
}

.section3 .row1 h4 {
    color: $primary-color;
    font-family: $primary-font;
    font-size: 1.8rem;
    max-width: 30rem;
    line-height: 1.6;
    margin: 0;
}

.section3 .row1 h4 span {
    color: $secondary-color;
}

.section3 .row1 a {
    font-size: 2.8rem;
    font-family: $primary-font;
    color: $secondary-color;
    text-decoration: none;
    font-size: inherit;
}

.section3 .row1 .time {
    font-size: 2.8rem;
    color: $primary-color;
    font-family: $primary-font;
}

.section3 .row1 .column2 {
    display: flex;
    flex-direction: column;
}

.section3 .row1 .column2 p {
    font-size: 10px;
    color: $secondary-color;
    font-family: $primary-font;
    font-weight: 700;
    letter-spacing: .2rem;
    margin-right: .4rem;
    text-align: right;
    margin-bottom: .5rem;
}

.section3 .row2 {
    padding: 35.5vh 0;
    flex-direction: column;
}

.section3 .row2 h2 {
    font-size: 5rem;
    font-weight: 500;
    font-family: $primary-font;
    color: $primary-color;
    text-align: center;
}

.section3 .row2 a {
    font-size: 2rem;
    color: $primary-color;
    font-family: $primary-font;
    text-align: center;
    text-decoration: none;
    transition: 0.3s;
}

.section3 .row2 a:hover {
    color: $secondary-color;
}

.section3 .row3 {
    display: flex;
    justify-content: space-between;
}

.section3 .row3 .column2 .icons {
    width: 4.5rem;
    height: 4.5rem;
    background-size: contain;
    background-repeat: no-repeat;
    background-position: center;
    display: block;
    transition: 0.3s;
}

.section3 .row3 .column2 .icons:hover {
    background-color: $secondary-color;
}

.section3 .row3 .column2 {
    display: flex;
}

.section3 .row3 .column2 .linkedin {
    background-image: url('/scss/images/linkedin-icon.svg');
    margin-right: 2.4rem;
}

.section3 .row3 .column2 .github {
    background-image: url('/scss/images/github-icon.svg');
}

.section3 .row3 .column1 span {
    font-size: 1.5rem;
    font-weight: 300;
    color: $primary-color;
    font-family: $primary-font;
    max-width: 23.4rem;
    display: block;
}

.fadeInOut {
    transform: 0.5s;
    animation: fadeInOut 1s infinite;
}

@keyframes fadeInOut {
    0% {
        opacity: 1;
    }

    50% {
        opacity: 0;
    }

    100% {
        opacity: 1;
    }
}

